@import "font-awesome/scss/font-awesome";
@import "ed-grid/ed-grid";
@import "modal.scss";

$color-first: #007BDF;
$color-second: #006DC6;
$color-third: #00CBFF;
$color-accent: #FBA905;
$color-dark: #141618;
$color-border: #DFE0E0;
$color-text: #53575A;
$color-bg: #FAFDFF;
$body-font: Lato, 'sans-serif';
$heading-font: 'Open Sans', 'sans-serif';

// Font sizes
$fontSizes: (
  t0      : rem(40px),
  t1      : rem(36px),
  t2      : rem(30px),
  t3      : rem(24px),
  normal  : rem(20px),
  normal2  : rem(18px),
  small   : rem(16px),
  smaller : rem(14px),
);

// Get font size from map
@function fontSize($size) {
  @if map-has-key($fontSizes, $size) {
    @return map-get($fontSizes,$size)
  } @else {
    @error '$size is not valid, valid values are t1, t2, t3, normal, small, smaller'
  }
}

* {
	font-family: $body-font;
	font-size: fontSize(small);
}

body 
{
	background-color: $color-bg;
	color: $color-text;
}

.text-center {
	text-align: center;
}

#cabezera {
	height: 5rem;
	border-bottom: 1px solid $color-border;
	background: #FFF;
}

.logo {
	height: 4rem;
}

.contacto-menu .item-contacto {
	padding: .25rem 0;
	
	span {
		color: $color-second;
	}
}

.main-banner {
	background: $color-first;
	padding: 4rem 0 5rem;
	clip-path: polygon(0 0, 100% 0, 100% 100%, 0 94%);
	position: relative;

	@include from(l) {
		padding: 8rem 0 10rem;
	}

	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		opacity: 0.2;
	}
}

.main-banner-data {
	position: relative;
	color: #FFF;
	text-align: center;

	h1 {
		font-size: fontSize(normal);
		line-height: 1.5;
		margin-top: 1.25rem;

		@include from(m)
		{
			font-size: fontSize(t3);
		}

		@include from(l)
		{
			font-size: fontSize(t2);
		}
	}

	p {
		font-size: fontSize(smaller);

		@include from(m)
		{
			font-size: fontSize(normal);
		}
	}

	#boton-consulta {
		line-height: 3;
		background: #FFF;
		color: $color-first;
		display: inline-block;
		padding: 0 10px;
		text-decoration: none;
		font-weight: bold;
		transition: all ease-in .5s;
		border-radius: 5px;
		margin-top: 8px;
	}

	#boton-consulta:hover {
		box-shadow: 1px 1px 15px #53575A;
	}
}

#contenedor-principal {
	margin-top: 3rem;
}

#quienessomos {
	padding: 2rem 0;

	.lading-img {
		border-radius: 5px;
		box-shadow: 2px 2px 10px rgba(#000, .5);
	}
	
	.title {
		font-size: fontSize(t3);
	}

	.descripcion {
		line-height: 1.5;
		text-align: justify;
	}
}

#beneficios {
	background: $color-first;
	color: #FFF;
	margin: 2rem 0;
	padding: 2rem 0;
	position: relative;


	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		opacity: 0.1;
	}

	.title {
		text-align: center;
		font-size: fontSize(t2);
		padding-bottom: 10px;
		margin-bottom: 25px;
		margin-top: 25px;
		border-bottom: 1px solid #fff;
	}

	.modulos {
		margin: 0;
		line-height: 2;
		padding-bottom: 1.5rem;
		list-style: none;
		padding-left: 0;
	}

	.img-beneficios {
		border-radius: 5px;
		box-shadow: 2px 2px 10px rgba(#000, .5);
	}
}

#eligenos {
	margin: 3.5rem 0;
	padding: 2rem 0;

	.title {
		text-align: center;
		margin-top: 0;
		padding-bottom: 1rem;
		font-size: fontSize(t3);
	}

	.razones {
		margin: 0;
		line-height: 2;
		padding-bottom: 1.5rem;
	}

	.img-eligenos {
		border-radius: 5px;
		max-height: 20rem;
		box-shadow: 2px 2px 10px rgba(#000, .5);
	}

	.video-container {
		position: relative;
		padding-bottom: 56.25%;
		padding-top: 30px; height: 0; overflow: hidden;
		margin-bottom: 20px;
	}
	
	.video-container iframe,
	.video-container object,
	.video-container embed {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

#planes {
	.title {
		text-align: center;
		font-size: 2.8em;
		margin-bottom: 2.5rem;
	}
	
	.subtitle {
		text-align: center;
		font-weight: normal;
		margin-bottom: 2.5rem;
	}

	.plan {
		border: 1px solid $color-border;
		border-radius: 10px;
		margin-bottom: 20px;

		.price {
			text-align: center;
		}

		.detalles {
			list-style: none;
			padding: 0 40px;
			margin: 24px 0;

			li {
				line-height: 2;
			}
		}
	}
}

#clientes {
	background: $color-first;
	color: #FFF;
	margin: 2rem 0;
	padding: 2rem 0;
	position: relative;


	img.background {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		opacity: 0.1;
	}

	.title {
		text-align: center;
		font-size: fontSize(t2);
		margin-top: 1rem;
		padding-bottom: 10px;
		margin-bottom: 25px;
		border-bottom: 1px solid #fff;
	}

	.titleComprobantes {
		font-size: fontSize(normal2);
	}

	#fechaActual {
		margin: 0;
		font-size: fontSize(normal2);
		text-decoration: underline;
	}

	#buttonComprobantes {
		margin-bottom: 40px;
		margin: 1em;
		background: $color-accent;
		color: $color-bg;
		width: 200px;
		font-size: fontSize(normal)
	}

	.listClients {
		margin-top: 25px;

		.cliente {
			padding: 10px;
			background: #fff;
			opacity: 1;
			min-height: 200px;
			text-align: center;
			flex-direction: column;
			margin-bottom: 20px;
			border-radius: 10px;
			box-shadow: 1px 1px 10px rgba(#000, .5);
			cursor: default;

			.imageClient {
				display: inline-block;
				width: 120px;
				height: 120px;
			}

			.titleClient {
				display: block;
				color: $color-text;
				font-weight: bold;
				padding: 10px 0;
			}
		}
	}
}

#developers {
	margin-bottom: 80px;	

	.title {
		text-align: center;
		font-size: fontSize(t0);
		margin-bottom: 60px;
		margin-top: 70px;
	}

	.listDevelopers {
		margin-top: 5px;

		.developer {
			padding: 10px;
			background: #fff;
			opacity: 1;
			min-height: 200px;
			text-align: center;
			flex-direction: column;
			margin-bottom: 20px;
			border-radius: 10px;
			cursor: default;

			.imageDeveloper {
				display: inline-block;
				width: 120px;
				height: 120px;
				border-radius: 50%;
			}

			.titleDeveloper {
				display: block;
				color: $color-text;
				font-weight: bold;
				padding: 10px 0;
			}

			.positionDeveloper {
				display: block;
				color: $color-text;
				font-size: .9em;
				padding: 10px 0;
				padding-top: 0;
			}
		}
	}
}

#external {
	margin-bottom: 80px;	

	.titleGeneral {
		text-align: center;
		font-size: fontSize(t0);
		margin-bottom: 50px;
		margin-top: 40px;
	}
	.form-group	{
		margin-bottom: 10px;
	}

	.formatoImpresion {
		margin-top: 1em;
		padding: 1em 1em;
		background: rgba($color-border, .6);
		border-radius: 0 0 15px 15px;
		box-shadow: 2px 2px 5px rgba($color-dark, .2);
	}

	label {
		color: $color-text;
		font-weight: bold;
		display: block;
		text-align: left;
		width: 100%;
		margin-bottom: 10px;
	}

	input, select {
		padding: 10px;
		color: $color-text;
		border: 1px solid $color-border;
		border-radius: 5px;
		display: block;
		text-align: left;
		width: 100%;
	}

	.uppercase {
		text-transform: uppercase;
	}

	#sendRequest {
		background: $color-accent;
		display: block;
		margin:0;
		width: 100%;
		padding: .8em 1.6em;
		color: $color-bg;
		transition: all ease-in .3s;
		border-radius: 5px;
		line-height: 1.5;
	}

	#sendRequest:hover {
		text-decoration: underline;
	}

	button:hover {
		box-shadow: 1px 1px 10px rgba($color-border, 1);
	}

	#ventaswef, #ventasfe {
		display: block;
		overflow-x: auto;
	}

	table {
		overflow-x: auto;
		width: 100%;
		max-width: 100%;
		margin-bottom: 20px;
		margin-top: 20px;
		border-collapse: collapse;
		
		caption {
			font-weight: bold;
			font-size: fontSize(t3);
			line-height: 2;	
		}

		td {
			border: 1px solid $color-border;
			padding: 10px;
			font-size: fontSize(smaller) !important;

			.conforme {
				color: #fff;
				padding: 5px 10px;
				background: green;
				border-radius: 5px;
				font-size: .8em;
			}

			.anulado {
				color: #fff;
				padding: 5px 10px;
				background: red;
				border-radius: 5px;
				font-size: .8em;
			}
		}

		thead {
			font-weight: bold;
			color: #FFF;
			background: $color-first;

			td {
				padding: 15px;
				font-size: fontSize(small) !important;
			}
		}

		.buttonAction {
			display: inline-block;
			color: #333;
			background-color: #d4d4d4;
			border: 1px solid #8c8c8c;
			margin-bottom: 2px;
			border-radius: 3px;
			-webkit-box-shadow: none;
			box-shadow: none;
			padding: 1px 5px;
			font-size: 15px;
			line-height: 1.5;
		}

		.buttonAction:hover {
			background-color: #e7e7e7;
			border-color: #adadad;
		}

		& > tbody > tr:nth-of-type(odd) {
			background-color: #f9f9f9;
		}
	}
}

#modals {
	.btn__close {
		background: $color-accent;
		color: $color-bg;
	}
}

#footer {
	background: $color-dark;
	padding: 2rem 0;
	padding-bottom: 1rem;
	color: #FFF;
	
	.powered {
		text-align: center;
		font-size: fontSize(smaller);

		a {
			color: #FFF;
			font-weight: bold;
			text-decoration: underline;
		}
	}
	.contact-container {
		padding-top: 2rem;
	}

	.item-contact {
		border-left: 2px solid #FFF;
		padding-left: 1rem !important;
		padding: 1rem 0;
		line-height: 1.5;

		* {
			font-size: fontSize(smaller);
		}

		a {
			color: #FFF;
		}
	}
}

hr.separator {
	border: 0;
	height: 1px;
	background-image: linear-gradient(to right, rgba(#007bdf, 0), rgba(#007bdf, 0.75), rgba(#007bdf, 0));
}

.background-button {
	background: #fba905;
	padding: .8em 1.6em;
	font-size: fontSize(normal2);
}

.background-button:hover {
	background: #fba905;
}