$modal-padding: 15px;
$modal-border: 1px solid #e2e2e2;
$modal-backdrop: rgba(#000, .8);
$modal-aniamtion-speed: .35s;

.modal {
    $this: modal;
    
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 15px;
    overflow: auto;
    background-color: $modal-backdrop;
    animation-duration: $modal-aniamtion-speed;
    animation-fill-mode: both;
    animation-name: fadeIn;
    
    &__dialog {
        position: relative;
        max-width: 500px;
        padding: $modal-padding;
        margin: auto;
        border-radius: 4px;
        background-color: #fff;
    }
    
    &__close {
        position: absolute;
        top: 0;
        right: $modal-padding;
        padding: 0;
        border: none;
        color: #ccc;
        background-color: transparent;
        background-image: none;
        
        &:focus {
            outline: 0;
        }
    }
    
    &__header {
        border-bottom: $modal-border;
    }
    
    &__title {
        margin: 0 0 15px;
    }
    
    &__content {
        padding: 10px 0;
        font-size: 13px;
        line-height: 1.6;
        color: #555;
    }
    
    &__footer {
        padding-top: $modal-padding;
        border-top: $modal-border;
        text-align: right;

        .btn__close {
            margin: 0;
        }
    }
    
    /* Modifiers */
    &--fullscreen {
        padding: 5px;
        
        .#{$this}__dialog {
            width: 100%;
            max-width: none;
            height: 100%;   
            border-radius: 0;
        }
    }
    /* States */
    &.is-#{$this}-active {
        display: flex; 
    }
}

/* Animation */
@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}