/*
| Mixin para crear cuadriculas
|
| $gridItemSelector: nombre del selector CSS de cada item de la cuadricula
| $listColumns:
|              * lista de numeros de columnas en cada breakpoint (separados por espacios). Ej: 1 2 3 4
|              * el numero de columnas del ultimo breakpoint se hereda a breakpoints superiores
| $gutter: separación entre cada item de la cuadricula (opcional)
|
*/
/* Mixins de alineacion con compatibilidad hacia atras*/
:root {
  --color: #007BDF;
  --color-alt: #006DC6;
  --border-color: #DFE0E0;
  --light-bg: #F0F0F0;
  --border-radius: 0.25rem;
  --vertical-block-space: 1rem;
  --vertical-content-space: 1rem;
  --gap: 1rem;
  --max-width: 1200px;
}

@media screen and (min-width: 1024px) {
  :root {
    --vertical-block-space: 2rem;
    --gap: 2rem;
  }
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 15px;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
  animation-duration: 0.35s;
  animation-fill-mode: both;
  animation-name: fadeIn;
  /* Modifiers */
  /* States */
}

.modal__dialog {
  position: relative;
  max-width: 500px;
  padding: 15px;
  margin: auto;
  border-radius: 4px;
  background-color: #fff;
}

.modal__close {
  position: absolute;
  top: 0;
  right: 15px;
  padding: 0;
  border: none;
  color: #ccc;
  background-color: transparent;
  background-image: none;
}

.modal__close:focus {
  outline: 0;
}

.modal__header {
  border-bottom: 1px solid #e2e2e2;
}

.modal__title {
  margin: 0 0 15px;
}

.modal__content {
  padding: 10px 0;
  font-size: 13px;
  line-height: 1.6;
  color: #555;
}

.modal__footer {
  padding-top: 15px;
  border-top: 1px solid #e2e2e2;
  text-align: right;
}

.modal__footer .btn__close {
  margin: 0;
}

.modal--fullscreen {
  padding: 5px;
}

.modal--fullscreen .modal__dialog {
  width: 100%;
  max-width: none;
  height: 100%;
  border-radius: 0;
}

.modal.is-modal-active {
  display: flex;
}

/* Animation */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

