  border: 1px solid #DFE0E0;
  border-radius: 5px;
  display: block;
  text-align: left;
  width: 100%;
}

#external .uppercase {
  text-transform: uppercase;
}

#external #sendRequest {
  background: #FBA905;
  display: block;
  margin: 0;
  width: 100%;
  padding: .8em 1.6em;
  color: #FAFDFF;
  transition: all ease-in .3s;
  border-radius: 5px;
  line-height: 1.5;
}

#external #sendRequest:hover {
  text-decoration: underline;
}

#external button:hover {
  box-shadow: 1px 1px 10px #dfe0e0;
}

#external #ventaswef, #external #ventasfe {
  display: block;
  overflow-x: auto;
}

#external table {
  overflow-x: auto;
  width: 100%;
  max-width: 100%;
  margin-bottom: 20px;
  margin-top: 20px;
  border-collapse: collapse;
}

#external table caption {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 2;
}

#external table td {
  border: 1px solid #DFE0E0;
  padding: 10px;
  font-size: 0.875rem !important;
}

#external table td .conforme {
  color: #fff;
  padding: 5px 10px;
  background: green;
  border-radius: 5px;
  font-size: .8em;
}

#external table td .anulado {
  color: #fff;
  padding: 5px 10px;
  background: red;
  border-radius: 5px;
  font-size: .8em;
}

#external table thead {
  font-weight: bold;
  color: #FFF;
  background: #007BDF;
}

#external table thead td {
  padding: 15px;
  font-size: 1rem !important;
}

#external table .buttonAction {
  display: inline-block;
  color: #333;
  background-color: #d4d4d4;
  border: 1px solid #8c8c8c;
  margin-bottom: 2px;
  border-radius: 3px;
  -webkit-box-shadow: none;
  box-shadow: none;
  padding: 1px 5px;
  font-size: 15px;
  line-height: 1.5;
}

#external table .buttonAction:hover {
  background-color: #e7e7e7;
  border-color: #adadad;
}

#external table > tbody > tr:nth-of-type(odd) {
  background-color: #f9f9f9;
}

#modals .btn__close {
  background: #FBA905;
  color: #FAFDFF;
}

#footer {
  background: #141618;
  padding: 2rem 0;
  padding-bottom: 1rem;
  color: #FFF;
}

#footer .powered {
  text-align: center;
  font-size: 0.875rem;
}

#footer .powered a {
  color: #FFF;
  font-weight: bold;
  text-decoration: underline;
}

#footer .contact-container {
  padding-top: 2rem;
}

#footer .item-contact {
  border-left: 2px solid #FFF;
  padding-left: 1rem !important;
  padding: 1rem 0;
  line-height: 1.5;
}

#footer .item-contact * {
  font-size: 0.875rem;
}

#footer .item-contact a {
  color: #FFF;
}

hr.separator {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 123, 223, 0), rgba(0, 123, 223, 0.75), rgba(0, 123, 223, 0));
}

.background-button {
  background: #fba905;
  padding: .8em 1.6em;
  font-size: 1.125rem;
}

.background-button:hover {
  background: #fba905;
}
